import React from 'react';
import Grid from '@mui/material/Grid';
import { Link, withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import EquipmentDetails from '../EquipmentDetailStatus/EquipmentDetails';
import AssetManagement from '../AssetManagement/AssetManagement';
import Potal from '../../images/Portal.PNG';
import Equipment from '../../images/Equipment.PNG';
import AssetManagementImg from '../../images/AssetManagement.PNG';
import Inventory from '../../images/Inventory.PNG';
import Sales from '../../images/Sales.PNG';
import { EquipmentStatus, AssetManagementTab } from '../../../constants';
const Tabs = (props) => {
  const {
    match: {
      params: { tab },
    },
  } = props;
  console.log(tab);
  return (
    <Grid container spacing={2} pt={7}>
      <Grid item xs={2}>
        <Link to="/">
          <img src={Potal} alt="PortalDashboard" />
          <div>PortalDashboard</div>
        </Link>
        <Link to={`/Portal/${EquipmentStatus}`}>
          <div>
            <img src={Equipment} alt="Equipment" />
            <div>Equipment Status</div>
          </div>
        </Link>
        <Link to={`/Portal/${AssetManagementTab}`}>
          <div>
            <img src={AssetManagementImg} alt="AssetManagement" />
            <div>AssetManagement</div>
          </div>
        </Link>
        <div>
          <img src={Inventory} alt="Inventory" />
          <div>Inventory Management</div>
        </div>
        <div>
          <img src={Sales} alt="Sales" />
          <div>Sales</div>
        </div>
      </Grid>
      <Grid item xs={10} pr={{ sm: 0, md: 12 }}>
        {tab === EquipmentStatus && <EquipmentDetails />}
        {tab === AssetManagementTab && <AssetManagement />}
      </Grid>
    </Grid>
  );
};
Tabs.propTypes = {
  match: PropTypes.object,
};
Tabs.defaultProps = {
  match: {},
};
export default withRouter(Tabs);
